import React, { Component } from 'react'

import GoBack from "../../components/GoBack/GoBack"
export default class Food extends Component {
    constructor(){
        super()
        this.state={
            foods:[
                {foodId:1,name:"麻辣烫"},
                {foodId:2,name:"麻辣小龙虾"},
                {foodId:3,name:"麻辣香锅"},
            ]
        }
    }
    toDetail(foodId){
        this.props.history.push("/foodDetail?id="+foodId)
    }
    render() {
        const {foods}=this.state
        return (
            <div>
                <GoBack></GoBack>
                <ul>
                    {
                        foods.map(item=>{
                        return <li onClick={()=>this.toDetail(item.foodId)} key={item.foodId}>{item.name}</li>
                        })
                    }
                </ul>
            </div>
        )
    }
}
